import React, { PropsWithChildren } from 'react';
import { View, Image, ScrollView, useWindowDimensions } from 'react-native';
import PhoneFrame from './PhoneFrame';
import useDeviceSize, { DEVICE_SIZES } from '../hooks/useDeviceSize';
import useTheme from '../hooks/useTheme';
import { usePopup } from '../navigation/popup/PopupProvider';
import Text from './Text';
import Button from './buttons/Button';
import { Project } from '../types/Project';
import CloseButton from './CloseButton';

interface Props {
  title?: string;
  text?: string;
  project?: Project;
}

const PopupContent = ({ title, text, children, project }: PropsWithChildren<Props>) => {
  const theme = useTheme();
  const { width: screenWidth } = useWindowDimensions();
  const { closePopup } = usePopup();

  const deviceSize = useDeviceSize();
  const mobileView = deviceSize < DEVICE_SIZES.m;

  const maxFrameWidth = mobileView ? screenWidth - theme.space.m * 6 : undefined;

  return (
    <View
      style={{
        flex: 1,
      }}
    >
      <ScrollView
        style={{
          flex: 1,
        }}
        contentContainerStyle={{
          paddingVertical: theme.space.l,
          paddingHorizontal: theme.space.l,
        }}
      >
        <View
          style={{
            flexDirection: mobileView ? 'column' : 'row',
            alignItems: mobileView ? 'center' : undefined,
          }}
        >
          <View
            style={{
              marginBottom: mobileView ? theme.space.m : undefined,
              alignItems: 'center',
            }}
          >
            {project?.appScreenshotSources && (
              <PhoneFrame images={project.appScreenshotSources} frameWidth={maxFrameWidth}></PhoneFrame>
            )}
            {/* {project?.appVideoSources && <ReactPlayer url={project?.appVideoSources[0]} />} */}
            {(project?.appScreenshotSources?.length || 0) > 0 && (
              <Text style={{ fontStyle: 'italic', textAlign: 'center', marginBottom: theme.space.m }}>
                Swipe horizontally for more images
              </Text>
            )}
          </View>
          <View style={{ flex: 1, alignItems: 'center' }}>
            <View style={{ width: '100%', paddingLeft: project?.appScreenshotSources ? theme.space.m : 0 }}>
              <Text
                style={{
                  textAlign: 'center',
                  marginBottom: theme.space.m,
                  fontSize: 24,
                  fontWeight: 'bold',
                }}
              >
                {title}
              </Text>

              {project?.imageSources && (
                <Image
                  source={project.imageSources[0]}
                  style={{
                    backgroundColor: 'red',
                    flex: 1,
                    aspectRatio: 1.66,
                    marginBottom: theme.space.m,
                    borderRadius: 8,
                    overflow: 'hidden',
                  }}
                  resizeMode="cover"
                />
              )}
              {(project?.appStoreURL || project?.playStoreURL || project?.githubURL) && (
                <View
                  style={{
                    gap: theme.space.s,
                    justifyContent: 'stretch',
                    marginBottom: theme.space.m,
                    alignItems: 'center',
                  }}
                >
                  {project.playStoreURL && (
                    <Button
                      icon="google-play"
                      text="Download from google play"
                      link={project.playStoreURL}
                      style={{ minWidth: 250 }}
                    />
                  )}
                  {project.appStoreURL && (
                    <Button
                      icon="apple"
                      text="Download from App Store"
                      link={project.appStoreURL}
                      style={{ minWidth: 250 }}
                    />
                  )}
                  {project.githubURL && (
                    <Button
                      icon="github"
                      text="Find on GitHub"
                      link={project.githubURL}
                      style={{ minWidth: 250 }}
                    />
                  )}
                </View>
              )}
              {!!text && <Text style={{ textAlign: 'left', lineHeight: 26 }}>{text}</Text>}
            </View>
          </View>
        </View>

        {children}
        {project?.appPreview}
      </ScrollView>
      <View
        style={{
          position: 'absolute',
          right: 0,
          top: 0,
        }}
      >
        <CloseButton onClose={closePopup} />
      </View>
    </View>
  );
};
export default PopupContent;
